import api from './axios';
import type {
  InstagramAccount,
  InstagramPost,
  InstagramComment,
  ScrapeProfileRequest,
  ScrapeProfileResponse,
  ScrapePostsRequest,
  ScrapePostsResponse,
  ScrapeCommentsRequest,
  ScrapeCommentsResponse,
  AnalyzePostCommentsResponse,
} from '@/types/instagram';

// ============================================
// Instagram Account Service
// ============================================

export const instagramService = {
  /**
   * Get all Instagram accounts for current user
   */
  async getAccounts(params?: {
    skip?: number;
    limit?: number;
  }): Promise<InstagramAccount[]> {
    const response = await api.get<InstagramAccount[]>('/instagram-accounts/', {
      params,
    });
    return response.data;
  },

  /**
   * Get single Instagram account by ID
   */
  async getAccount(id: string): Promise<InstagramAccount> {
    const response = await api.get<InstagramAccount>(`/instagram-accounts/${id}`);
    return response.data;
  },

  /**
   * Get Instagram account by username
   */
  async getAccountByUsername(username: string): Promise<InstagramAccount> {
    const response = await api.get<InstagramAccount>(`/instagram-accounts/username/${username}`);
    return response.data;
  },

  /**
   * Scrape Instagram profile(s) using Apify
   */
  async scrapeProfiles(data: ScrapeProfileRequest): Promise<ScrapeProfileResponse> {
    const response = await api.post<ScrapeProfileResponse>('/instagram-accounts/scrape', data);
    return response.data;
  },

  /**
   * Delete Instagram account
   */
  async deleteAccount(id: string): Promise<void> {
    await api.delete(`/instagram-accounts/${id}`);
  },

  // ============================================
  // Instagram Posts Methods
  // ============================================

  /**
   * Get all Instagram posts for an account
   */
  async getPostsByAccount(
    accountId: string,
    params?: { skip?: number; limit?: number }
  ): Promise<InstagramPost[]> {
    const response = await api.get<InstagramPost[]>(
      `/instagram-posts/account/${accountId}`,
      { params }
    );
    return response.data;
  },

  /**
   * Get all Instagram posts for current user (all accounts)
   */
  async getAllPosts(): Promise<InstagramPost[]> {
    // First get all accounts, then fetch posts for each
    const accounts = await this.getAccounts();
    const postsPromises = accounts.map((acc) => this.getPostsByAccount(acc.id));
    const postsArrays = await Promise.all(postsPromises);
    return postsArrays.flat();
  },

  /**
   * Get single Instagram post by ID
   */
  async getPost(postId: string): Promise<InstagramPost> {
    const response = await api.get<InstagramPost>(`/instagram-posts/${postId}`);
    return response.data;
  },

  /**
   * Scrape Instagram posts using Apify
   */
  async scrapePosts(data: ScrapePostsRequest): Promise<ScrapePostsResponse> {
    const response = await api.post<ScrapePostsResponse>('/instagram-posts/scrape', data);
    return response.data;
  },

  /**
   * Delete Instagram post
   */
  async deletePost(postId: string): Promise<void> {
    await api.delete(`/instagram-posts/${postId}`);
  },

  // ============================================
  // Instagram Comments Methods
  // ============================================

  /**
   * Get all comments for a post
   */
  async getCommentsByPost(
    postId: string,
    params?: { skip?: number; limit?: number }
  ): Promise<InstagramComment[]> {
    const response = await api.get<InstagramComment[]>(
      `/instagram-comments/post/${postId}`,
      { params }
    );
    return response.data;
  },

  /**
   * Get single comment by ID
   */
  async getComment(commentId: string): Promise<InstagramComment> {
    const response = await api.get<InstagramComment>(`/instagram-comments/${commentId}`);
    return response.data;
  },

  /**
   * Scrape comments for posts
   */
  async scrapeComments(data: ScrapeCommentsRequest): Promise<ScrapeCommentsResponse> {
    const response = await api.post<ScrapeCommentsResponse>('/instagram-comments/scrape', data);
    return response.data;
  },

  /**
   * Delete a comment
   */
  async deleteComment(commentId: string): Promise<void> {
    await api.delete(`/instagram-comments/${commentId}`);
  },

  // ============================================
  // Sentiment Analysis Methods
  // ============================================

  /**
   * Analyze all comments for a post using IndoBERT
   */
  async analyzePostCommentsIndoBERT(postId: string): Promise<AnalyzePostCommentsResponse> {
    const response = await api.post<AnalyzePostCommentsResponse>(
      `/indobert/analyze-post/${postId}`
    );
    return response.data;
  },

  /**
   * Analyze all comments for a post using Lexicon
   */
  async analyzePostCommentsLexicon(postId: string): Promise<AnalyzePostCommentsResponse> {
    const response = await api.post<AnalyzePostCommentsResponse>(
      `/lexicon/analyze-post/${postId}`
    );
    return response.data;
  },

  /**
   * Analyze single text using IndoBERT
   */
  async analyzeTextIndoBERT(text: string): Promise<{
    text: string;
    label: string;
    score: number;
    scores: Record<string, number>;
  }> {
    const response = await api.post('/indobert/predict/single', { text });
    return response.data;
  },

  /**
   * Analyze single text using Lexicon
   */
  async analyzeTextLexicon(text: string): Promise<{
    text: string;
    label: string;
    score: number;
    tokens: string[];
    matched_words: Record<string, number>;
  }> {
    const response = await api.post('/lexicon/predict/single', { text });
    return response.data;
  },
};
