import { useState, useEffect, useCallback, useMemo } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { DashboardLayout } from '@/components/layout';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table';
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from '@/components/ui/dropdown-menu';
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from '@/components/ui/dialog';
import {
  ArrowLeft,
  Heart,
  MessageCircle,
  TrendingUp,
  Smile,
  Frown,
  Meh,
  Download,
  MoreHorizontal,
  Trash2,
  Sparkles,
  Loader2,
  ExternalLink,
  BadgeCheck,
} from 'lucide-react';
import { toast } from 'sonner';
import { instagramService } from '@/services';
import { getProxyImageUrl } from '@/lib/utils';
import type { InstagramPost, InstagramComment, InstagramAccount, SentimentSummary } from '@/types/instagram';

export function PostDetailPage() {
  const { postId } = useParams<{ postId: string }>();
  const navigate = useNavigate();

  const [post, setPost] = useState<InstagramPost | null>(null);
  const [account, setAccount] = useState<InstagramAccount | null>(null);
  const [comments, setComments] = useState<InstagramComment[]>([]);
  const [isLoading, setIsLoading] = useState(true);
  const [isLoadingComments, setIsLoadingComments] = useState(true);

  // Dialog states
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);
  const [selectedComment, setSelectedComment] = useState<InstagramComment | null>(null);
  const [isDeleting, setIsDeleting] = useState(false);

  // Scrape & Analyze states
  const [isScraping, setIsScraping] = useState(false);
  const [isAnalyzing, setIsAnalyzing] = useState(false);
  const [analyzeMethod, setAnalyzeMethod] = useState<'indobert' | 'lexicon'>('indobert');

  // Calculate sentiment summary
  const sentimentSummary: SentimentSummary = useMemo(() => {
    const commentsWithSentiment = comments.filter((c) => c.sentiment_label);
    return {
      positive: commentsWithSentiment.filter((c) => c.sentiment_label === 'POSITIVE').length,
      neutral: commentsWithSentiment.filter((c) => c.sentiment_label === 'NEUTRAL').length,
      negative: commentsWithSentiment.filter((c) => c.sentiment_label === 'NEGATIVE').length,
    };
  }, [comments]);

  const totalAnalyzed = sentimentSummary.positive + sentimentSummary.neutral + sentimentSummary.negative;

  // Fetch post and account data
  const fetchPostData = useCallback(async () => {
    if (!postId) return;

    try {
      setIsLoading(true);
      const postData = await instagramService.getPost(postId);
      setPost(postData);

      // Fetch account info
      const accountData = await instagramService.getAccount(postData.instagram_account_id);
      setAccount(accountData);
    } catch (error) {
      console.error('Failed to fetch post:', error);
      toast.error('Gagal memuat data postingan');
    } finally {
      setIsLoading(false);
    }
  }, [postId]);

  // Fetch comments
  const fetchComments = useCallback(async () => {
    if (!postId) return;

    try {
      setIsLoadingComments(true);
      const data = await instagramService.getCommentsByPost(postId);
      setComments(data);
    } catch (error) {
      console.error('Failed to fetch comments:', error);
      toast.error('Gagal memuat komentar');
    } finally {
      setIsLoadingComments(false);
    }
  }, [postId]);

  useEffect(() => {
    fetchPostData();
    fetchComments();
  }, [fetchPostData, fetchComments]);

  const formatNumber = (num: number): string => {
    return new Intl.NumberFormat('id-ID').format(num);
  };

  const getPercentage = (value: number, total: number): string => {
    if (total === 0) return '0';
    return ((value / total) * 100).toFixed(1);
  };

  const getSentimentBadge = (label: string | null) => {
    switch (label) {
      case 'POSITIVE':
        return <Badge className="bg-green-500 hover:bg-green-600">Positif</Badge>;
      case 'NEGATIVE':
        return <Badge className="bg-red-500 hover:bg-red-600">Negatif</Badge>;
      case 'NEUTRAL':
        return <Badge className="bg-yellow-500 hover:bg-yellow-600 text-black">Netral</Badge>;
      default:
        return <Badge variant="outline">Belum dianalisis</Badge>;
    }
  };

  const handleBack = () => {
    navigate('/instagram/posts');
  };

  const handleScrapeComments = async () => {
    if (!post) return;

    setIsScraping(true);
    try {
      const response = await instagramService.scrapeComments({
        post_urls: [post.url],
        results_limit: 50,
      });

      if (response.success) {
        toast.success('Komentar berhasil di-scrape!', {
          description: `${response.comments_created} komentar baru ditambahkan.`,
        });
        await fetchComments();
      } else {
        toast.error('Gagal scrape komentar', {
          description: response.errors?.join(', ') || 'Terjadi kesalahan.',
        });
      }
    } catch (error) {
      console.error('Failed to scrape comments:', error);
      toast.error('Gagal scrape komentar');
    } finally {
      setIsScraping(false);
    }
  };

  const handleAnalyzeComments = async (method: 'indobert' | 'lexicon') => {
    if (!postId) return;

    setAnalyzeMethod(method);
    setIsAnalyzing(true);

    try {
      const response = method === 'indobert'
        ? await instagramService.analyzePostCommentsIndoBERT(postId)
        : await instagramService.analyzePostCommentsLexicon(postId);

      if (response.success) {
        toast.success('Analisis sentimen selesai!', {
          description: `${response.analyzed_count} komentar berhasil dianalisis.`,
        });
        await fetchComments();
      } else {
        toast.error('Gagal menganalisis komentar');
      }
    } catch (error) {
      console.error('Failed to analyze comments:', error);
      toast.error('Gagal menganalisis komentar');
    } finally {
      setIsAnalyzing(false);
    }
  };

  const handleDeleteClick = (comment: InstagramComment) => {
    setSelectedComment(comment);
    setDeleteDialogOpen(true);
  };

  const handleConfirmDelete = async () => {
    if (selectedComment) {
      setIsDeleting(true);
      try {
        await instagramService.deleteComment(selectedComment.id);
        setComments(comments.filter((c) => c.id !== selectedComment.id));
        toast.success('Komentar berhasil dihapus');
        setDeleteDialogOpen(false);
        setSelectedComment(null);
      } catch {
        toast.error('Gagal menghapus komentar');
      } finally {
        setIsDeleting(false);
      }
    }
  };

  const handleOpenInstagram = () => {
    if (post) {
      window.open(post.url, '_blank');
    }
  };

  if (isLoading) {
    return (
      <DashboardLayout
        breadcrumbs={[
          { label: 'Dashboard', href: '/dashboard' },
          { label: 'Postingan', href: '/instagram/posts' },
          { label: 'Loading...' },
        ]}
      >
        <div className="flex items-center justify-center h-64">
          <Loader2 className="h-8 w-8 animate-spin text-muted-foreground" />
        </div>
      </DashboardLayout>
    );
  }

  if (!post || !account) {
    return (
      <DashboardLayout
        breadcrumbs={[
          { label: 'Dashboard', href: '/dashboard' },
          { label: 'Postingan', href: '/instagram/posts' },
          { label: 'Tidak Ditemukan' },
        ]}
      >
        <div className="flex flex-col items-center justify-center h-64 gap-4">
          <p className="text-muted-foreground">Postingan tidak ditemukan</p>
          <Button onClick={handleBack}>Kembali</Button>
        </div>
      </DashboardLayout>
    );
  }

  return (
    <DashboardLayout
      breadcrumbs={[
        { label: 'Dashboard', href: '/dashboard' },
        { label: 'Postingan', href: '/instagram/posts' },
        { label: `@${account.username}` },
      ]}
    >
      {/* Back Button */}
      <Button variant="outline" onClick={handleBack} className="w-fit">
        <ArrowLeft className="mr-2 h-4 w-4" />
        Kembali
      </Button>

      <div className="grid gap-6 lg:grid-cols-5">
        {/* Left Column - Post Info */}
        <Card className="lg:col-span-2">
          <CardContent>
            {/* Account Info */}
            <div className="flex items-center justify-between mb-4">
              <div className="flex items-center gap-3">
                <Avatar className="h-10 w-10 sm:h-12 sm:w-12">
                  <AvatarImage src={getProxyImageUrl(account.profile_pic_url)} alt={account.full_name} />
                  <AvatarFallback>
                    {account.full_name.split(' ').map((n) => n[0]).join('').slice(0, 2).toUpperCase()}
                  </AvatarFallback>
                </Avatar>
                <div className="min-w-0 flex-1">
                  <div className="flex items-center gap-1">
                    <span className="font-semibold text-xs sm:text-sm truncate">{account.full_name}</span>
                    {account.verified && (
                      <BadgeCheck className="h-4 w-4 text-blue-500 shrink-0" />
                    )}
                  </div>
                  <span className="text-xs sm:text-sm text-muted-foreground">@{account.username}</span>
                </div>
              </div>
              <Button variant="outline" size="sm" onClick={handleOpenInstagram} className="shrink-0">
                <ExternalLink className="md:mr-2 h-4 w-4" />
                <span className="hidden sm:inline">Instagram</span>
              </Button>
            </div>

            {/* Media */}
            <div className="relative aspect-3/4 bg-muted rounded-lg overflow-hidden mb-4">
              {post.video_url ? (
                <video
                  src={getProxyImageUrl(post.video_url)}
                  controls
                  className="w-full h-full object-cover"
                  poster={getProxyImageUrl(post.display_url)}
                />
              ) : post.display_url ? (
                <img
                  src={getProxyImageUrl(post.display_url)}
                  alt="Post"
                  className="w-full h-full object-cover"
                />
              ) : (
                <div className="flex items-center justify-center h-full text-muted-foreground">
                  Tidak ada media
                </div>
              )}
            </div>

            {/* Caption */}
            <p className="text-xs mb-4 whitespace-pre-wrap">
              {post.caption || 'Tidak ada caption'}
            </p>

            {/* Stats */}
            <div className="flex items-center gap-4 sm:gap-6 text-muted-foreground">
              <div className="flex items-center gap-2">
                <Heart className="h-4 w-4 sm:h-5 sm:w-5 text-red-500" />
                <span className="font-medium text-sm sm:text-base">{formatNumber(post.likes_count)}</span>
              </div>
              <div className="flex items-center gap-2">
                <MessageCircle className="h-4 w-4 sm:h-5 sm:w-5" />
                <span className="font-medium text-sm sm:text-base">{formatNumber(post.comments_count)}</span>
              </div>
            </div>
          </CardContent>
        </Card>

        {/* Right Column - Sentiment Analysis */}
        <div className="lg:col-span-3 space-y-6">
          {/* Sentiment Summary Card */}
          <Card>
            <CardHeader>
              <CardTitle className="flex items-center gap-2">
                <TrendingUp className="h-5 w-5" />
                Analisis Sentimen
              </CardTitle>
            </CardHeader>
            <CardContent>
              <div className="grid grid-cols-3 gap-4">
                {/* Positive */}
                <div className="text-center">
                  <div className="flex items-center justify-center gap-2 text-green-500 mb-1">
                    <Smile className="h-5 w-5" />
                    <span className="font-medium">Positif</span>
                  </div>
                  <div className="text-2xl font-bold text-green-500">
                    {sentimentSummary.positive}
                  </div>
                  <div className="text-sm text-muted-foreground">
                    {getPercentage(sentimentSummary.positive, totalAnalyzed)}%
                  </div>
                </div>

                {/* Neutral */}
                <div className="text-center">
                  <div className="flex items-center justify-center gap-2 text-yellow-500 mb-1">
                    <Meh className="h-5 w-5" />
                    <span className="font-medium">Netral</span>
                  </div>
                  <div className="text-2xl font-bold text-yellow-500">
                    {sentimentSummary.neutral}
                  </div>
                  <div className="text-sm text-muted-foreground">
                    {getPercentage(sentimentSummary.neutral, totalAnalyzed)}%
                  </div>
                </div>

                {/* Negative */}
                <div className="text-center">
                  <div className="flex items-center justify-center gap-2 text-red-500 mb-1">
                    <Frown className="h-5 w-5" />
                    <span className="font-medium">Negatif</span>
                  </div>
                  <div className="text-2xl font-bold text-red-500">
                    {sentimentSummary.negative}
                  </div>
                  <div className="text-sm text-muted-foreground">
                    {getPercentage(sentimentSummary.negative, totalAnalyzed)}%
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Comments List Card */}
          <Card>
            <CardHeader className="flex flex-row items-center justify-between">
              <CardTitle className="flex items-center gap-2">
                <MessageCircle className="h-5 w-5" />
                Daftar Komentar ({comments.length})
              </CardTitle>
              <Button onClick={handleScrapeComments} disabled={isScraping} size="sm" className="sm:w-auto">
                {isScraping ? (
                  <Loader2 className="md:mr-2 h-4 w-4 animate-spin" />
                ) : (
                  <Download className="md:mr-2 h-4 w-4" />
                )}
                <span className="hidden sm:inline">Scrape Komentar</span>
              </Button>
            </CardHeader>
            <CardContent>
              {isLoadingComments ? (
                <div className="flex items-center justify-center h-32">
                  <Loader2 className="h-8 w-8 animate-spin text-muted-foreground" />
                </div>
              ) : comments.length === 0 ? (
                <div className="flex flex-col items-center justify-center h-32 text-muted-foreground">
                  <MessageCircle className="h-8 w-8 mb-2" />
                  <p>Belum ada komentar</p>
                  <p className="text-sm">Klik "Scrape Komentar" untuk mengambil komentar</p>
                </div>
              ) : (
                <>
                  {/* Desktop Table */}
                  <div className="hidden md:block overflow-x-auto">
                    <Table>
                      <TableHeader>
                        <TableRow>
                          <TableHead>Pengguna</TableHead>
                          <TableHead className="min-w-50">Komentar</TableHead>
                          <TableHead>Sentimen</TableHead>
                          <TableHead className="text-right">Likes</TableHead>
                          <TableHead className="text-center">Aksi</TableHead>
                        </TableRow>
                      </TableHeader>
                      <TableBody>
                        {comments.map((comment) => (
                          <TableRow key={comment.id}>
                            <TableCell>
                              <div className="flex items-center gap-2">
                                <Avatar className="h-8 w-8">
                                  <AvatarFallback>
                                    {comment.owner_username[0].toUpperCase()}
                                  </AvatarFallback>
                                </Avatar>
                                <span className="text-xs font-medium">
                                  @{comment.owner_username}
                                </span>
                              </div>
                            </TableCell>
                            <TableCell>
                              <p className="text-xs line-clamp-2">{comment.text}</p>
                            </TableCell>
                            <TableCell>
                              {getSentimentBadge(comment.sentiment_label)}
                            </TableCell>
                            <TableCell className="text-right">
                              <div className="flex items-center justify-end gap-1">
                                <Heart className="h-4 w-4 text-muted-foreground" />
                                <span>{comment.likes_count}</span>
                              </div>
                            </TableCell>
                            <TableCell className="text-center">
                              <DropdownMenu>
                                <DropdownMenuTrigger asChild>
                                  <Button variant="ghost" size="icon">
                                    <MoreHorizontal className="h-4 w-4" />
                                  </Button>
                                </DropdownMenuTrigger>
                                <DropdownMenuContent align="end">
                                  <DropdownMenuItem
                                    onClick={() => handleAnalyzeComments('indobert')}
                                    disabled={isAnalyzing}
                                  >
                                    <Sparkles className="mr-2 h-4 w-4" />
                                    Analisis IndoBERT
                                  </DropdownMenuItem>
                                  <DropdownMenuItem
                                    onClick={() => handleAnalyzeComments('lexicon')}
                                    disabled={isAnalyzing}
                                  >
                                    <Sparkles className="mr-2 h-4 w-4" />
                                    Analisis Lexicon
                                  </DropdownMenuItem>
                                  <DropdownMenuItem
                                    onClick={() => handleDeleteClick(comment)}
                                    className="text-red-600 focus:text-red-600"
                                  >
                                    <Trash2 className="mr-2 h-4 w-4" />
                                    Hapus
                                  </DropdownMenuItem>
                                </DropdownMenuContent>
                              </DropdownMenu>
                            </TableCell>
                          </TableRow>
                        ))}
                      </TableBody>
                    </Table>
                  </div>

                  {/* Mobile Cards */}
                  <div className="md:hidden space-y-4">
                    {comments.map((comment) => (
                      <div key={comment.id} className="border rounded-lg p-4 bg-card">
                        <div className="flex items-start justify-between mb-3">
                          <div className="flex items-center gap-2 min-w-0 flex-1">
                            <Avatar className="h-8 w-8 shrink-0">
                              <AvatarFallback>
                                {comment.owner_username[0].toUpperCase()}
                              </AvatarFallback>
                            </Avatar>
                            <div className="min-w-0 flex-1">
                              <span className="text-xs font-medium truncate">
                                @{comment.owner_username}
                              </span>
                              <div className="flex items-center gap-2 mt-1">
                                {getSentimentBadge(comment.sentiment_label)}
                                <div className="flex items-center gap-1 text-xs text-muted-foreground">
                                  <Heart className="h-3 w-3" />
                                  <span>{comment.likes_count}</span>
                                </div>
                              </div>
                            </div>
                          </div>
                          <DropdownMenu>
                            <DropdownMenuTrigger asChild>
                              <Button variant="ghost" size="icon" className="shrink-0">
                                <MoreHorizontal className="h-4 w-4" />
                              </Button>
                            </DropdownMenuTrigger>
                            <DropdownMenuContent align="end">
                              <DropdownMenuItem
                                onClick={() => handleAnalyzeComments('indobert')}
                                disabled={isAnalyzing}
                              >
                                <Sparkles className="mr-2 h-4 w-4" />
                                Analisis IndoBERT
                              </DropdownMenuItem>
                              <DropdownMenuItem
                                onClick={() => handleAnalyzeComments('lexicon')}
                                disabled={isAnalyzing}
                              >
                                <Sparkles className="mr-2 h-4 w-4" />
                                Analisis Lexicon
                              </DropdownMenuItem>
                              <DropdownMenuItem
                                onClick={() => handleDeleteClick(comment)}
                                className="text-red-600 focus:text-red-600"
                              >
                                <Trash2 className="mr-2 h-4 w-4" />
                                Hapus
                              </DropdownMenuItem>
                            </DropdownMenuContent>
                          </DropdownMenu>
                        </div>
                        <p className="text-xs text-foreground">{comment.text}</p>
                      </div>
                    ))}
                  </div>
                </>
              )}

              {/* Analyze All Button */}
              {comments.length > 0 && (
                <div className="flex flex-col sm:flex-row justify-end gap-2 mt-4 pt-4 border-t">
                  <Button
                    variant="outline"
                    onClick={() => handleAnalyzeComments('lexicon')}
                    disabled={isAnalyzing}
                    size="sm"
                    className="w-full sm:w-auto"
                  >
                    {isAnalyzing && analyzeMethod === 'lexicon' ? (
                      <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                    ) : (
                      <Sparkles className="mr-2 h-4 w-4" />
                    )}
                    Analisis Lexicon
                  </Button>
                  <Button
                    onClick={() => handleAnalyzeComments('indobert')}
                    disabled={isAnalyzing}
                    size="sm"
                    className="w-full sm:w-auto"
                  >
                    {isAnalyzing && analyzeMethod === 'indobert' ? (
                      <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                    ) : (
                      <Sparkles className="mr-2 h-4 w-4" />
                    )}
                    Analisis IndoBERT
                  </Button>
                </div>
              )}
            </CardContent>
          </Card>
        </div>
      </div>

      {/* Delete Confirmation Dialog */}
      <Dialog open={deleteDialogOpen} onOpenChange={setDeleteDialogOpen}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>Hapus Komentar</DialogTitle>
            <DialogDescription>
              Apakah Anda yakin ingin menghapus komentar ini? Tindakan ini tidak dapat dibatalkan.
            </DialogDescription>
          </DialogHeader>
          {selectedComment && (
            <div className="py-4">
              <p className="text-sm text-muted-foreground">
                <span className="font-medium">@{selectedComment.owner_username}:</span>{' '}
                {selectedComment.text}
              </p>
            </div>
          )}
          <DialogFooter className="flex-col sm:flex-row gap-2">
            <Button variant="outline" onClick={() => setDeleteDialogOpen(false)} className="w-full sm:w-auto">
              Batal
            </Button>
            <Button variant="destructive" onClick={handleConfirmDelete} disabled={isDeleting} className="w-full sm:w-auto">
              {isDeleting ? (
                <>
                  <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                  Menghapus...
                </>
              ) : (
                'Hapus'
              )}
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </DashboardLayout>
  );
}

export default PostDetailPage;
