import { useState } from 'react';
import { DashboardLayout } from '@/components/layout';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { BookOpen, Loader2, Smile, Frown, Meh } from 'lucide-react';
import { toast } from 'sonner';
import { instagramService } from '@/services';

interface AnalysisResult {
  text: string;
  label: string;
  score: number;
  tokens: string[];
  matched_words: Record<string, number>;
}

export function LexiconPage() {
  const [inputText, setInputText] = useState('');
  const [isAnalyzing, setIsAnalyzing] = useState(false);
  const [result, setResult] = useState<AnalysisResult | null>(null);

  const handleAnalyze = async () => {
    if (!inputText.trim()) {
      toast.error('Masukkan teks terlebih dahulu');
      return;
    }

    setIsAnalyzing(true);
    setResult(null);

    try {
      const response = await instagramService.analyzeTextLexicon(inputText);
      setResult(response);
    } catch (error) {
      console.error('Failed to analyze:', error);
      toast.error('Gagal menganalisis teks');
    } finally {
      setIsAnalyzing(false);
    }
  };

  const getSentimentIcon = (label: string) => {
    switch (label.toUpperCase()) {
      case 'POSITIF':
      case 'POSITIVE':
        return <Smile className="h-12 w-12 text-green-500" />;
      case 'NEGATIF':
      case 'NEGATIVE':
        return <Frown className="h-12 w-12 text-red-500" />;
      default:
        return <Meh className="h-12 w-12 text-yellow-500" />;
    }
  };

  const getSentimentBadge = (label: string) => {
    switch (label.toUpperCase()) {
      case 'POSITIF':
      case 'POSITIVE':
        return <Badge className="bg-green-500 hover:bg-green-600 text-lg px-4 py-1">Positif</Badge>;
      case 'NEGATIF':
      case 'NEGATIVE':
        return <Badge className="bg-red-500 hover:bg-red-600 text-lg px-4 py-1">Negatif</Badge>;
      default:
        return <Badge className="bg-yellow-500 hover:bg-yellow-600 text-black text-lg px-4 py-1">Netral</Badge>;
    }
  };

  const getScoreColor = (score: number) => {
    if (score > 0) return 'text-green-600 bg-green-100 dark:bg-green-900/30';
    if (score < 0) return 'text-red-600 bg-red-100 dark:bg-red-900/30';
    return 'text-gray-600 bg-gray-100 dark:bg-gray-800';
  };

  return (
    <DashboardLayout
      breadcrumbs={[
        { label: 'Dashboard', href: '/dashboard' },
        { label: 'Analisis Sentimen' },
        { label: 'Lexicon' },
      ]}
    >
      <div className="max-w-2xl mx-auto space-y-6">
        {/* Header */}
        <div className="flex items-center gap-3">
          <div className="p-2 rounded-lg bg-blue-100 dark:bg-blue-900">
            <BookOpen className="h-6 w-6 text-blue-600 dark:text-blue-400" />
          </div>
          <div>
            <h1 className="text-2xl font-bold">Analisis Lexicon</h1>
            <p className="text-muted-foreground">
              Uji analisis sentimen menggunakan pendekatan Lexicon (Kamus Kata)
            </p>
          </div>
        </div>

        {/* Input Card */}
        <Card>
          <CardHeader>
            <CardTitle>Masukkan Teks</CardTitle>
            <CardDescription>
              Ketik atau tempel teks yang ingin dianalisis sentimennya
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <textarea
              className="w-full min-h-32 p-3 border rounded-lg resize-none focus:outline-none focus:ring-2 focus:ring-blue-500 dark:bg-gray-800 dark:border-gray-700"
              placeholder="Contoh: Pelayanannya sangat memuaskan dan ramah!"
              value={inputText}
              onChange={(e) => setInputText(e.target.value)}
            />
            <Button
              onClick={handleAnalyze}
              disabled={isAnalyzing || !inputText.trim()}
              className="w-full"
            >
              {isAnalyzing ? (
                <>
                  <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                  Menganalisis...
                </>
              ) : (
                <>
                  <BookOpen className="mr-2 h-4 w-4" />
                  Analisis dengan Lexicon
                </>
              )}
            </Button>
          </CardContent>
        </Card>

        {/* Result Card */}
        {result && (
          <Card>
            <CardHeader>
              <CardTitle>Hasil Analisis</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="flex flex-col items-center gap-4 py-4">
                {getSentimentIcon(result.label)}
                {getSentimentBadge(result.label)}
                <p className="text-sm text-muted-foreground text-center">
                  Total Skor: <span className="font-semibold">{result.score}</span>
                </p>
              </div>

              {/* Tokens */}
              <div className="mt-4 pt-4 border-t">
                <p className="text-sm font-medium mb-2">Token yang Diproses:</p>
                <div className="flex flex-wrap gap-2">
                  {result.tokens.map((token, index) => (
                    <Badge key={index} variant="outline" className="text-xs">
                      {token}
                    </Badge>
                  ))}
                </div>
              </div>

              {/* Matched Words */}
              {result.matched_words && Object.keys(result.matched_words).length > 0 && (
                <div className="mt-4 pt-4 border-t">
                  <p className="text-sm font-medium mb-3">Kata yang Cocok dengan Kamus:</p>
                  <div className="space-y-2">
                    {Object.entries(result.matched_words).map(([word, score]) => (
                      <div key={word} className="flex items-center justify-between p-2 rounded-lg bg-muted/50">
                        <span className="text-sm font-medium">{word}</span>
                        <span className={`text-sm font-semibold px-2 py-1 rounded ${getScoreColor(score)}`}>
                          {score > 0 ? '+' : ''}{score}
                        </span>
                      </div>
                    ))}
                  </div>
                </div>
              )}
            </CardContent>
          </Card>
        )}
      </div>
    </DashboardLayout>
  );
}

export default LexiconPage;
