import axiosInstance, { setStoredToken, removeStoredToken } from './axios';
import type { User, Token, LoginRequest, RegisterRequest, UserUpdateRequest } from '@/types/auth';

/**
 * API Endpoints for Authentication
 */
const AUTH_ENDPOINTS = {
  LOGIN: '/users/login',
  REGISTER: '/users/register',
  ME: '/users/me',
  USERS: '/users',
} as const;

/**
 * Authentication Service
 * Handles all auth-related API calls
 */
export const authService = {
  /**
   * Login user with username and password
   * Note: The API uses x-www-form-urlencoded for login
   */
  login: async (credentials: LoginRequest): Promise<Token> => {
    const formData = new URLSearchParams();
    formData.append('username', credentials.username);
    formData.append('password', credentials.password);

    const response = await axiosInstance.post<Token>(AUTH_ENDPOINTS.LOGIN, formData, {
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded',
      },
    });

    // Store token after successful login
    if (response.data.access_token) {
      setStoredToken(response.data.access_token);
    }

    return response.data;
  },

  /**
   * Register a new user
   */
  register: async (data: RegisterRequest): Promise<User> => {
    const response = await axiosInstance.post<User>(AUTH_ENDPOINTS.REGISTER, data);
    return response.data;
  },

  /**
   * Get current user profile
   */
  getCurrentUser: async (): Promise<User> => {
    const response = await axiosInstance.get<User>(AUTH_ENDPOINTS.ME);
    return response.data;
  },

  /**
   * Get user by ID
   */
  getUserById: async (userId: string): Promise<User> => {
    const response = await axiosInstance.get<User>(`${AUTH_ENDPOINTS.USERS}/${userId}`);
    return response.data;
  },

  /**
   * Update user
   */
  updateUser: async (userId: string, data: UserUpdateRequest): Promise<User> => {
    const response = await axiosInstance.put<User>(`${AUTH_ENDPOINTS.USERS}/${userId}`, data);
    return response.data;
  },

  /**
   * Delete user
   */
  deleteUser: async (userId: string): Promise<void> => {
    await axiosInstance.delete(`${AUTH_ENDPOINTS.USERS}/${userId}`);
  },

  /**
   * Logout user - clear stored token
   */
  logout: (): void => {
    removeStoredToken();
  },
};

export default authService;
