import { clsx, type ClassValue } from "clsx"
import { twMerge } from "tailwind-merge"

export function cn(...inputs: ClassValue[]) {
  return twMerge(clsx(inputs))
}

/**
 * Proxy Instagram CDN images through local server to avoid CORS issues
 * @param url - The original Instagram CDN URL
 * @returns Proxied URL or undefined if no URL provided
 */
export function getProxyImageUrl(url: string | null | undefined): string | undefined {
  if (!url) return undefined;
  
  // Check if URL is from Instagram CDN (various domains)
  const instagramCdnPatterns = [
    /scontent[^.]*\.cdninstagram\.com/,
    /instagram\.[^.]+\.fna\.fbcdn\.net/,
    /scontent[^.]*\.fbcdn\.net/,
  ];
  
  const isInstagramCdn = instagramCdnPatterns.some(pattern => pattern.test(url));
  
  if (isInstagramCdn) {
    // Use the proxy path with encoded URL
    return `/instagram-proxy?url=${encodeURIComponent(url)}`;
  }
  
  return url;
}

/**
 * Format number with Indonesian locale
 */
export function formatNumber(num: number): string {
  return new Intl.NumberFormat('id-ID').format(num);
}

/**
 * Format date with Indonesian locale
 */
export function formatDate(dateString: string, options?: Intl.DateTimeFormatOptions): string {
  const defaultOptions: Intl.DateTimeFormatOptions = {
    day: '2-digit',
    month: 'short',
    year: 'numeric',
  };
  return new Date(dateString).toLocaleDateString('id-ID', options || defaultOptions);
}

/**
 * Calculate percentage
 */
export function getPercentage(value: number, total: number): string {
  if (total === 0) return '0';
  return ((value / total) * 100).toFixed(1);
}