import { useState, useEffect, useCallback } from 'react';
import { DashboardLayout } from '@/components/layout';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Badge } from '@/components/ui/badge';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table';
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from '@/components/ui/dropdown-menu';
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from '@/components/ui/dialog';
import {
  Instagram,
  TrendingUp,
  // Image,
  BadgeCheck,
  Search,
  Plus,
  MoreHorizontal,
  RefreshCw,
  Trash2,
  ExternalLink,
  Loader2,
} from 'lucide-react';
import { toast } from 'sonner';
import { instagramService } from '@/services';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import { getProxyImageUrl } from '@/lib/utils';
import type { InstagramAccount, InstagramAccountStats } from '@/types/instagram';

export function InstagramAccountsPage() {
  const [accounts, setAccounts] = useState<InstagramAccount[]>([]);
  const [searchQuery, setSearchQuery] = useState('');
  const [isLoading, setIsLoading] = useState(true);
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);
  const [selectedAccount, setSelectedAccount] = useState<InstagramAccount | null>(null);
  const [isDeleting, setIsDeleting] = useState(false);
  
  // Connect account dialog state
  const [connectDialogOpen, setConnectDialogOpen] = useState(false);
  const [usernameInput, setUsernameInput] = useState('');
  const [isScraping, setIsScraping] = useState(false);

  // Calculate stats from accounts
  const stats: InstagramAccountStats = {
    total_accounts: accounts.length,
    total_followers: accounts.reduce((sum, acc) => sum + acc.followers_count, 0),
    total_posts: accounts.reduce((sum, acc) => sum + acc.posts_count, 0),
    verified_accounts: accounts.filter((acc) => acc.verified).length,
  };

  // Fetch accounts from API
  const fetchAccounts = useCallback(async () => {
    try {
      setIsLoading(true);
      const data = await instagramService.getAccounts();
      setAccounts(data);
    } catch (error) {
      console.error('Failed to fetch accounts:', error);
      toast.error('Gagal memuat data akun', {
        description: 'Silakan coba lagi atau refresh halaman.',
      });
    } finally {
      setIsLoading(false);
    }
  }, []);

  useEffect(() => {
    fetchAccounts();
  }, [fetchAccounts]);

  // Filter accounts based on search query
  const filteredAccounts = accounts.filter(
    (account) =>
      account.username.toLowerCase().includes(searchQuery.toLowerCase()) ||
      account.full_name.toLowerCase().includes(searchQuery.toLowerCase())
  );

  const formatNumber = (num: number): string => {
    return new Intl.NumberFormat('id-ID').format(num);
  };

  const formatDate = (dateString: string): string => {
    return new Date(dateString).toLocaleDateString('id-ID', {
      day: 'numeric',
      month: 'numeric',
      year: 'numeric',
    });
  };

  const handleConnectAccount = () => {
    setConnectDialogOpen(true);
  };

  const handleCloseConnectDialog = () => {
    setConnectDialogOpen(false);
    setUsernameInput('');
  };

  const handleScrapeAccount = async () => {
    // Clean username (remove @ if present)
    const cleanUsername = usernameInput.trim().replace(/^@/, '');
    
    if (!cleanUsername) {
      toast.error('Username tidak boleh kosong');
      return;
    }

    // Check if account already exists
    if (accounts.some((acc) => acc.username.toLowerCase() === cleanUsername.toLowerCase())) {
      toast.error('Akun sudah terhubung', {
        description: `@${cleanUsername} sudah ada dalam daftar akun.`,
      });
      return;
    }

    setIsScraping(true);

    try {
      const response = await instagramService.scrapeProfiles({
        usernames: [cleanUsername],
      });

      if (response.success) {
        toast.success('Akun berhasil ditambahkan!', {
          description: response.message,
        });
        // Refresh accounts list
        await fetchAccounts();
        handleCloseConnectDialog();
      } else {
        toast.error('Gagal mengambil data akun', {
          description: response.errors?.join(', ') || 'Pastikan username benar dan akun tidak di-private.',
        });
      }
    } catch (error: unknown) {
      const errorMessage = error instanceof Error ? error.message : 'Pastikan username benar dan akun tidak di-private.';
      toast.error('Gagal mengambil data akun', {
        description: errorMessage,
      });
    } finally {
      setIsScraping(false);
    }
  };

  const handleRefreshAccount = async (account: InstagramAccount) => {
    toast.info(`Memperbarui data @${account.username}...`);
    try {
      await instagramService.scrapeProfiles({ usernames: [account.username] });
      await fetchAccounts();
      toast.success(`Data @${account.username} berhasil diperbarui`);
    } catch {
      toast.error('Gagal memperbarui data akun');
    }
  };

  const handleDeleteClick = (account: InstagramAccount) => {
    setSelectedAccount(account);
    setDeleteDialogOpen(true);
  };

  const handleConfirmDelete = async () => {
    if (selectedAccount) {
      setIsDeleting(true);
      try {
        await instagramService.deleteAccount(selectedAccount.id);
        setAccounts(accounts.filter((a) => a.id !== selectedAccount.id));
        toast.success('Akun berhasil dihapus', {
          description: `@${selectedAccount.username} telah dihapus dari daftar.`,
        });
        setDeleteDialogOpen(false);
        setSelectedAccount(null);
      } catch {
        toast.error('Gagal menghapus akun');
      } finally {
        setIsDeleting(false);
      }
    }
  };

  const handleOpenInstagram = (username: string) => {
    window.open(`https://instagram.com/${username}`, '_blank');
  };

  const statsCards = [
    {
      title: 'Akun Instagram',
      value: formatNumber(stats.total_accounts),
      description: 'Akun terhubung',
      icon: Instagram,
      iconColor: 'text-pink-500',
    },
    {
      title: 'Total Pengikut',
      value: formatNumber(stats.total_followers),
      description: 'Di semua akun',
      icon: TrendingUp,
      iconColor: 'text-blue-500',
    },
  ];

  return (
    <DashboardLayout
      breadcrumbs={[
        { label: 'Dashboard', href: '/dashboard' },
        { label: 'Media Sosial' },
      ]}
    >
      {/* Stats Cards */}
      <div className="grid gap-4 grid-cols-2 md:grid-cols-2">
        {statsCards.map((stat) => (
          <Card key={stat.title}>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium text-muted-foreground">
                {stat.title}
              </CardTitle>
              <stat.icon className={`h-4 w-4 ${stat.iconColor}`} />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{stat.value}</div>
              <p className="text-xs text-muted-foreground">{stat.description}</p>
            </CardContent>
          </Card>
        ))}
      </div>

      {/* Search and Actions */}
      <div className="flex flex-col gap-4 sm:flex-row sm:items-center sm:justify-between">
        <div className="relative w-full sm:w-72">
          <Search className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-muted-foreground" />
          <Input
            placeholder="Cari akun..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            className="pl-9"
          />
        </div>
        <div className="flex gap-2 justify-between w-full sm:w-auto">
          <Button variant="outline" onClick={fetchAccounts} disabled={isLoading}>
            <RefreshCw className={`mr-2 h-4 w-4 ${isLoading ? 'animate-spin' : ''}`} />
            Refresh
          </Button>
          <Button onClick={handleConnectAccount}>
            <Plus className="mr-2 h-4 w-4" />
            Hubungkan Akun
          </Button>
        </div>
      </div>

      {/* Accounts Table/Card List */}
      <Card>
        <CardHeader>
          <CardTitle>Akun Instagram Terhubung</CardTitle>
        </CardHeader>
        <CardContent>
          {isLoading ? (
            <div className="flex items-center justify-center h-32">
              <Loader2 className="h-8 w-8 animate-spin text-muted-foreground" />
            </div>
          ) : (
            <div className="block w-full overflow-x-auto">
              {/* Table for Desktop */}
              <Table className="hidden min-w-175 md:table">
                <TableHeader>
                  <TableRow>
                    <TableHead className="w-75">Akun</TableHead>
                    <TableHead>Username</TableHead>
                    <TableHead className="text-right">Pengikut</TableHead>
                    <TableHead className="text-right">Mengikuti</TableHead>
                    <TableHead className="text-right">Postingan</TableHead>
                    <TableHead className="text-center">Tipe Akun</TableHead>
                    <TableHead>Terhubung</TableHead>
                    <TableHead className="text-center">Aksi</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {filteredAccounts.length === 0 ? (
                    <TableRow>
                      <TableCell colSpan={8} className="h-24 text-center">
                        {searchQuery
                          ? 'Tidak ada akun yang cocok dengan pencarian.'
                          : 'Belum ada akun Instagram yang terhubung.'}
                      </TableCell>
                    </TableRow>
                  ) : (
                    filteredAccounts.map((account) => (
                      <TableRow key={account.id}>
                        <TableCell>
                          <div className="flex items-center gap-3">
                            <Avatar className="h-10 w-10">
                              <AvatarImage src={getProxyImageUrl(account.profile_pic_url)} alt={account.full_name} />
                              <AvatarFallback>
                                {account.full_name.split(' ').map((n) => n[0]).join('').slice(0, 2).toUpperCase()}
                              </AvatarFallback>
                            </Avatar>
                            <div className="flex flex-col">
                              <span className="font-medium flex items-center">{account.full_name} {account.verified && <BadgeCheck className="ml-1 h-4 w-4 text-purple-500" />}</span>
                              <span className="text-sm text-muted-foreground truncate max-w-50">
                                {account.biography || '-'}
                              </span>
                            </div>
                          </div>
                        </TableCell>
                        <TableCell className="text-muted-foreground">
                          @{account.username}
                        </TableCell>
                        <TableCell className="text-right">
                          {formatNumber(account.followers_count)}
                        </TableCell>
                        <TableCell className="text-right">
                          {formatNumber(account.follows_count)}
                        </TableCell>
                        <TableCell className="text-right">
                          {formatNumber(account.posts_count)}
                        </TableCell>
                        <TableCell className="text-center">
                          <div className="flex flex-col items-center gap-1">
                            <Badge
                              variant="default"
                              className={account.is_business_account ? 'bg-blue-500 hover:bg-blue-600' : 'bg-gray-500 hover:bg-gray-600'}
                            >
                              {account.is_business_account ? 'Bisnis' : 'Personal'}
                            </Badge>
                          </div>
                        </TableCell>
                        <TableCell>{formatDate(account.created_at)}</TableCell>
                        <TableCell className="text-center">
                          <DropdownMenu>
                            <DropdownMenuTrigger asChild>
                              <Button variant="ghost" size="icon">
                                <MoreHorizontal className="h-4 w-4" />
                                <span className="sr-only">Menu</span>
                              </Button>
                            </DropdownMenuTrigger>
                            <DropdownMenuContent align="end">
                              <DropdownMenuItem
                                onClick={() => handleOpenInstagram(account.username)}
                              >
                                <ExternalLink className="mr-2 h-4 w-4" />
                                Buka Instagram
                              </DropdownMenuItem>
                              <DropdownMenuItem
                                onClick={() => handleRefreshAccount(account)}
                              >
                                <RefreshCw className="mr-2 h-4 w-4" />
                                Perbarui Data
                              </DropdownMenuItem>
                              <DropdownMenuItem
                                onClick={() => handleDeleteClick(account)}
                                className="text-red-600 focus:text-red-600"
                              >
                                <Trash2 className="mr-2 h-4 w-4" />
                                Hapus Akun
                              </DropdownMenuItem>
                            </DropdownMenuContent>
                          </DropdownMenu>
                        </TableCell>
                      </TableRow>
                    ))
                  )}
                </TableBody>
              </Table>
              {/* Card List for Mobile */}
              <div className="flex flex-col gap-3 md:hidden">
                {filteredAccounts.length === 0 ? (
                  <div className="text-center text-muted-foreground py-8">
                    {searchQuery
                      ? 'Tidak ada akun yang cocok dengan pencarian.'
                      : 'Belum ada akun Instagram yang terhubung.'}
                  </div>
                ) : (
                  filteredAccounts.map((account) => (
                    <div
                      key={account.id}
                      className="rounded-lg border bg-card p-4 flex flex-col gap-2 shadow-sm"
                    >
                      <div className="flex items-center gap-3">
                        <Avatar className="h-10 w-10">
                          <AvatarImage src={getProxyImageUrl(account.profile_pic_url)} alt={account.full_name} />
                          <AvatarFallback>
                            {account.full_name.split(' ').map((n) => n[0]).join('').slice(0, 2).toUpperCase()}
                          </AvatarFallback>
                        </Avatar>
                        <div className="flex flex-col min-w-0 flex-1">
                          <span className="font-medium wrap-break-words flex items-center">{account.full_name} {account.verified && <BadgeCheck className="ml-1 h-4 w-4 text-purple-500" />}</span>
                          <span className="text-xs text-muted-foreground truncate max-w-50">
                            {account.biography || '-'}
                          </span>
                          <span className="text-xs text-muted-foreground break-all">@{account.username}</span>
                        </div>
                      </div>
                      <div className="flex flex-row flex-wrap gap-4 mt-2 text-xs">
                        <div>
                          <span className="font-semibold">Pengikut:</span> {formatNumber(account.followers_count)}
                        </div>
                        <div>
                          <span className="font-semibold">Mengikuti:</span> {formatNumber(account.follows_count)}
                        </div>
                        <div>
                          <span className="font-semibold">Postingan:</span> {formatNumber(account.posts_count)}
                        </div>
                        <div>
                          <span className="font-semibold">Terhubung:</span> {formatDate(account.created_at)}
                        </div>
                      </div>
                      <div className="ml-auto flex gap-1 mt-2">
                        <Button size="icon" variant="ghost" className="h-7 w-7" onClick={() => handleOpenInstagram(account.username)}>
                          <ExternalLink className="h-4 w-4" />
                        </Button>
                        {/* <Button size="icon" variant="ghost" className="h-7 w-7" onClick={() => handleRefreshAccount(account)}>
                          <RefreshCw className="h-4 w-4" />
                        </Button> */}
                        <Button size="icon" variant="ghost" className="h-7 w-7 text-red-600" onClick={() => handleDeleteClick(account)}>
                          <Trash2 className="h-4 w-4" />
                        </Button>
                      </div>
                    </div>
                  ))
                )}
              </div>
            </div>
          )}
        </CardContent>
      </Card>

      {/* Delete Confirmation Dialog */}
      <Dialog open={deleteDialogOpen} onOpenChange={setDeleteDialogOpen}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>Hapus Akun Instagram</DialogTitle>
            <DialogDescription>
              Apakah Anda yakin ingin menghapus akun{' '}
              <span className="font-medium">@{selectedAccount?.username}</span>? Tindakan
              ini tidak dapat dibatalkan.
            </DialogDescription>
          </DialogHeader>
          <DialogFooter>
            <Button variant="outline" onClick={() => setDeleteDialogOpen(false)}>
              Batal
            </Button>
            <Button variant="destructive" onClick={handleConfirmDelete} disabled={isDeleting}>
              {isDeleting ? (
                <>
                  <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                  Menghapus...
                </>
              ) : (
                'Hapus'
              )}
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>

      {/* Connect Account Dialog */}
      <Dialog open={connectDialogOpen} onOpenChange={setConnectDialogOpen}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>Hubungkan Akun Instagram</DialogTitle>
            <DialogDescription>
              Masukkan username Instagram untuk mengambil data profil secara otomatis.
            </DialogDescription>
          </DialogHeader>
          <div className="space-y-4 py-4">
            <div className="space-y-2">
              <Label htmlFor="username">Username Instagram</Label>
              <div className="flex gap-2">
                <Input
                  id="username"
                  placeholder="johndoe"
                  value={usernameInput}
                  onChange={(e) => setUsernameInput(e.target.value)}
                  onKeyDown={(e) => {
                    if (e.key === 'Enter' && !isScraping) {
                      handleScrapeAccount();
                    }
                  }}
                  disabled={isScraping}
                />
                <Button 
                  onClick={handleScrapeAccount} 
                  disabled={isScraping || !usernameInput.trim()}
                >
                  {isScraping ? (
                    <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                  ) : (
                    <Search className="mr-2 h-4 w-4" />
                  )}
                  {isScraping ? 'Memproses...' : 'Scrape'}
                </Button>
              </div>
              <p className="text-sm text-muted-foreground">
                Contoh: johndoe atau @johndoe
              </p>
            </div>
          </div>
          <DialogFooter>
            <Button variant="outline" onClick={handleCloseConnectDialog} disabled={isScraping}>
              Batal
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </DashboardLayout>
  );
}

export default InstagramAccountsPage;
