import { useState, useEffect, useCallback } from 'react';
import { useNavigate } from 'react-router-dom';
import { DashboardLayout } from '@/components/layout';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table';
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuTrigger,
} from '@/components/ui/dropdown-menu';
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from '@/components/ui/dialog';
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select';
import {
  Image,
  Heart,
  MessageCircle,
  Search,
  Download,
  MoreHorizontal,
  Trash2,
  ExternalLink,
  Loader2,
  RefreshCw,
  Eye,
} from 'lucide-react';
import { toast } from 'sonner';
import { instagramService } from '@/services';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import { getProxyImageUrl } from '@/lib/utils';
import type { InstagramPost, InstagramPostStats, InstagramAccount } from '@/types/instagram';

export function InstagramPostsPage() {
  const navigate = useNavigate();
  const [posts, setPosts] = useState<InstagramPost[]>([]);
  const [accounts, setAccounts] = useState<InstagramAccount[]>([]);
  const [searchQuery, setSearchQuery] = useState('');
  const [selectedAccountId, setSelectedAccountId] = useState<string>('all');
  const [isLoading, setIsLoading] = useState(true);
  const [deleteDialogOpen, setDeleteDialogOpen] = useState(false);
  const [selectedPost, setSelectedPost] = useState<InstagramPost | null>(null);
  const [isDeleting, setIsDeleting] = useState(false);

  // Scrape posts dialog state
  const [scrapeDialogOpen, setScrapeDialogOpen] = useState(false);
  const [scrapeUsername, setScrapeUsername] = useState('');
  const [scrapeLimit, setScrapeLimit] = useState('10');
  const [isScraping, setIsScraping] = useState(false);

  // Calculate stats from posts
  const stats: InstagramPostStats = {
    total_posts: posts.length,
    total_likes: posts.reduce((sum, post) => sum + post.likes_count, 0),
    total_comments: posts.reduce((sum, post) => sum + post.comments_count, 0),
  };

  // Fetch accounts
  const fetchAccounts = useCallback(async () => {
    try {
      const data = await instagramService.getAccounts();
      setAccounts(data);
    } catch (error) {
      console.error('Failed to fetch accounts:', error);
    }
  }, []);

  // Fetch posts
  const fetchPosts = useCallback(async () => {
    try {
      setIsLoading(true);
      let data: InstagramPost[];

      if (selectedAccountId === 'all') {
        data = await instagramService.getAllPosts();
      } else {
        data = await instagramService.getPostsByAccount(selectedAccountId);
      }

      // Sort by timestamp descending (newest first)
      data.sort((a, b) => new Date(b.timestamp).getTime() - new Date(a.timestamp).getTime());
      setPosts(data);
    } catch (error) {
      console.error('Failed to fetch posts:', error);
      toast.error('Gagal memuat data postingan', {
        description: 'Silakan coba lagi atau refresh halaman.',
      });
    } finally {
      setIsLoading(false);
    }
  }, [selectedAccountId]);

  useEffect(() => {
    fetchAccounts();
  }, [fetchAccounts]);

  useEffect(() => {
    fetchPosts();
  }, [fetchPosts]);

  // Filter posts based on search query
  const filteredPosts = posts.filter(
    (post) =>
      (post.caption?.toLowerCase().includes(searchQuery.toLowerCase()) ?? false) ||
      post.owner_username.toLowerCase().includes(searchQuery.toLowerCase()) ||
      post.owner_full_name.toLowerCase().includes(searchQuery.toLowerCase())
  );

  const formatNumber = (num: number): string => {
    return new Intl.NumberFormat('id-ID').format(num);
  };

  const formatDate = (dateString: string): string => {
    return new Date(dateString).toLocaleDateString('id-ID', {
      day: '2-digit',
      month: 'short',
      year: 'numeric',
    });
  };

  const truncateCaption = (caption: string | null, maxLength: number = 80): string => {
    if (!caption) return '-';
    if (caption.length <= maxLength) return caption;
    return caption.substring(0, maxLength) + '...';
  };

  const handleScrapeDialogOpen = () => {
    // Pre-fill with first account username if available
    if (accounts.length > 0) {
      setScrapeUsername(accounts[0].username);
    }
    setScrapeDialogOpen(true);
  };

  const handleScrapeDialogClose = () => {
    setScrapeDialogOpen(false);
    setScrapeUsername('');
    setScrapeLimit('10');
  };

  const handleScrapePosts = async () => {
    const cleanUsername = scrapeUsername.trim().replace(/^@/, '');

    if (!cleanUsername) {
      toast.error('Username tidak boleh kosong');
      return;
    }

    // Check if account exists
    const accountExists = accounts.some(
      (acc) => acc.username.toLowerCase() === cleanUsername.toLowerCase()
    );

    if (!accountExists) {
      toast.error('Akun tidak ditemukan', {
        description: 'Pastikan akun sudah terhubung terlebih dahulu di halaman Media Sosial.',
      });
      return;
    }

    setIsScraping(true);

    try {
      const response = await instagramService.scrapePosts({
        usernames: [cleanUsername],
        results_limit: parseInt(scrapeLimit) || 10,
      });

      if (response.success) {
        toast.success('Postingan berhasil di-scrape!', {
          description: `${response.posts_created} postingan baru ditambahkan.`,
        });
        await fetchPosts();
        handleScrapeDialogClose();
      } else {
        toast.error('Gagal scrape postingan', {
          description: response.errors?.join(', ') || 'Terjadi kesalahan saat scraping.',
        });
      }
    } catch (error: unknown) {
      const errorMessage = error instanceof Error ? error.message : 'Pastikan akun sudah terhubung dan tidak di-private.';
      toast.error('Gagal scrape postingan', {
        description: errorMessage,
      });
    } finally {
      setIsScraping(false);
    }
  };

  const handleDeleteClick = (post: InstagramPost) => {
    setSelectedPost(post);
    setDeleteDialogOpen(true);
  };

  const handleConfirmDelete = async () => {
    if (selectedPost) {
      setIsDeleting(true);
      try {
        await instagramService.deletePost(selectedPost.id);
        setPosts(posts.filter((p) => p.id !== selectedPost.id));
        toast.success('Postingan berhasil dihapus');
        setDeleteDialogOpen(false);
        setSelectedPost(null);
      } catch {
        toast.error('Gagal menghapus postingan');
      } finally {
        setIsDeleting(false);
      }
    }
  };

  const handleOpenInstagram = (url: string) => {
    window.open(url, '_blank');
  };

  const statsCards = [
    {
      title: 'Total Postingan',
      value: formatNumber(stats.total_posts),
      description: 'Semua postingan',
      icon: Image,
      iconColor: 'text-blue-500',
    },
    {
      title: 'Total Komentar',
      value: formatNumber(stats.total_comments),
      description: 'Total komentar',
      icon: MessageCircle,
      iconColor: 'text-green-500',
    },
  ];

  return (
    <DashboardLayout
      breadcrumbs={[
        { label: 'Dashboard', href: '/dashboard' },
        { label: 'Postingan' },
      ]}
    >
      {/* Stats Cards */}
      <div className="grid gap-4 grid-cols-2">
        {statsCards.map((stat) => (
          <Card key={stat.title}>
            <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
              <CardTitle className="text-sm font-medium text-muted-foreground">
                {stat.title}
              </CardTitle>
              <stat.icon className={`h-4 w-4 ${stat.iconColor}`} />
            </CardHeader>
            <CardContent>
              <div className="text-2xl font-bold">{stat.value}</div>
              <p className="text-xs text-muted-foreground">{stat.description}</p>
            </CardContent>
          </Card>
        ))}
      </div>

      {/* Search and Filters */}
      <div className="flex flex-col gap-4 sm:flex-row sm:items-center sm:justify-between">
        <div className="flex flex-col gap-4 sm:flex-row sm:items-center w-full">
          <div className="relative w-full sm:w-64">
            <Search className="absolute left-3 top-1/2 h-4 w-4 -translate-y-1/2 text-muted-foreground" />
            <Input
              placeholder="Cari postingan..."
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              className="pl-9"
            />
          </div>
          <Select value={selectedAccountId} onValueChange={setSelectedAccountId}>
            <SelectTrigger className="w-full sm:w-45">
              <SelectValue placeholder="Semua Akun" />
            </SelectTrigger>
            <SelectContent>
              <SelectItem value="all">Semua Akun</SelectItem>
              {accounts.map((account) => (
                <SelectItem key={account.id} value={account.id}>
                  @{account.username}
                </SelectItem>
              ))}
            </SelectContent>
          </Select>
        </div>
        <div className="flex gap-2 justify-between w-full sm:w-auto">
          <Button variant="outline" onClick={fetchPosts} disabled={isLoading}>
            <RefreshCw className={`mr-2 h-4 w-4 ${isLoading ? 'animate-spin' : ''}`} />
            Refresh
          </Button>
          <Button onClick={handleScrapeDialogOpen}>
            <Download className="mr-2 h-4 w-4" />
            Scrape Postingan
          </Button>
        </div>
      </div>

      {/* Posts Table/Card List */}
      <Card>
        <CardHeader>
          <CardTitle>Postingan Instagram</CardTitle>
        </CardHeader>
        <CardContent>
          {isLoading ? (
            <div className="flex items-center justify-center h-32">
              <Loader2 className="h-8 w-8 animate-spin text-muted-foreground" />
            </div>
          ) : (
            <div className="block w-full overflow-x-auto">
              <Table className="hidden min-w-175 md:table">
                <TableHeader>
                  <TableRow>
                    <TableHead className="w-100">Caption</TableHead>
                    <TableHead>Akun</TableHead>
                    <TableHead>Likes</TableHead>
                    <TableHead>Komentar</TableHead>
                    <TableHead>Tanggal Post</TableHead>
                    <TableHead className="text-center">Aksi</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {filteredPosts.length === 0 ? (
                    <TableRow>
                      <TableCell colSpan={6} className="h-24 text-center">
                        {searchQuery
                          ? 'Tidak ada postingan yang cocok dengan pencarian.'
                          : 'Belum ada postingan. Scrape postingan untuk memulai.'}
                      </TableCell>
                    </TableRow>
                  ) : (
                    filteredPosts.map((post) => (
                      <TableRow
                        key={post.id}
                        className="cursor-pointer hover:bg-muted/50"
                        onClick={() => navigate(`/instagram/posts/${post.id}`)}
                      >
                        <TableCell>
                          <span className="text-sm">
                            {truncateCaption(post.caption)}
                          </span>
                        </TableCell>
                        <TableCell>
                          <div className="flex items-center gap-2">
                            <Avatar className="h-8 w-8">
                              <AvatarImage src={getProxyImageUrl(accounts.find((a) => a.id === post.instagram_account_id)?.profile_pic_url)} alt={post.owner_full_name} />
                              <AvatarFallback>
                                {post.owner_full_name.split(' ').map((n) => n[0]).join('').slice(0, 2).toUpperCase()}
                              </AvatarFallback>
                            </Avatar>
                            <div className="flex flex-col">
                              <span className="text-sm font-medium">{post.owner_full_name}</span>
                              <span className="text-xs text-muted-foreground">@{post.owner_username}</span>
                            </div>
                          </div>
                        </TableCell>
                        <TableCell>
                          <div className="flex items-center justify-start gap-1">
                            <Heart className="h-4 w-4 text-red-500" />
                            <span>{formatNumber(post.likes_count)}</span>
                          </div>
                        </TableCell>
                        <TableCell>
                          <div className="flex items-center justify-start gap-1">
                            <MessageCircle className="h-4 w-4 text-muted-foreground" />
                            <span>{formatNumber(post.comments_count)}</span>
                          </div>
                        </TableCell>
                        <TableCell>{formatDate(post.timestamp)}</TableCell>
                        <TableCell className="text-center" onClick={(e) => e.stopPropagation()}>
                          <DropdownMenu>
                            <DropdownMenuTrigger asChild>
                              <Button variant="ghost" size="icon">
                                <MoreHorizontal className="h-4 w-4" />
                                <span className="sr-only">Menu</span>
                              </Button>
                            </DropdownMenuTrigger>
                            <DropdownMenuContent align="end">
                              <DropdownMenuItem
                                onClick={() => navigate(`/instagram/posts/${post.id}`)}
                              >
                                <Eye className="mr-2 h-4 w-4" />
                                Lihat Detail
                              </DropdownMenuItem>
                              <DropdownMenuItem
                                onClick={() => handleOpenInstagram(post.url)}
                              >
                                <ExternalLink className="mr-2 h-4 w-4" />
                                Buka di Instagram
                              </DropdownMenuItem>
                              <DropdownMenuItem
                                onClick={() => handleDeleteClick(post)}
                                className="text-red-600 focus:text-red-600"
                              >
                                <Trash2 className="mr-2 h-4 w-4" />
                                Hapus Postingan
                              </DropdownMenuItem>
                            </DropdownMenuContent>
                          </DropdownMenu>
                        </TableCell>
                      </TableRow>
                    ))
                  )}
                </TableBody>
              </Table>
              {/* Card List for Mobile */}
              <div className="flex flex-col gap-3 md:hidden">
                {filteredPosts.length === 0 ? (
                  <div className="text-center text-muted-foreground py-8">
                    {searchQuery
                      ? 'Tidak ada postingan yang cocok dengan pencarian.'
                      : 'Belum ada postingan. Scrape postingan untuk memulai.'}
                  </div>
                ) : (
                  filteredPosts.map((post) => (
                    <div
                      key={post.id}
                      className="rounded-lg border bg-card p-4 flex flex-col gap-2 shadow-sm cursor-pointer hover:bg-muted/50"
                      onClick={() => navigate(`/instagram/posts/${post.id}`)}
                    >
                      <div className="flex items-center gap-3">
                        <Avatar className="h-9 w-9">
                          <AvatarImage src={getProxyImageUrl(accounts.find((a) => a.id === post.instagram_account_id)?.profile_pic_url)} alt={post.owner_full_name} />
                          <AvatarFallback>
                            {post.owner_full_name.split(' ').map((n) => n[0]).join('').slice(0, 2).toUpperCase()}
                          </AvatarFallback>
                        </Avatar>
                        <div className="flex flex-col">
                          <span className="text-sm font-medium">{post.owner_full_name}</span>
                          <span className="text-xs text-muted-foreground">@{post.owner_username}</span>
                        </div>
                        <span className="ml-auto text-xs text-muted-foreground whitespace-nowrap">{formatDate(post.timestamp)}</span>
                      </div>
                      <div className="text-sm line-clamp-3 text-foreground/90">
                        {truncateCaption(post.caption, 120)}
                      </div>
                      <div className="flex items-center gap-4 mt-1">
                        <div className="flex items-center gap-1 text-xs">
                          <Heart className="h-4 w-4 text-red-500" />
                          <span>{formatNumber(post.likes_count)}</span>
                        </div>
                        <div className="flex items-center gap-1 text-xs">
                          <MessageCircle className="h-4 w-4 text-muted-foreground" />
                          <span>{formatNumber(post.comments_count)}</span>
                        </div>
                        <div className="ml-auto flex gap-1">
                          <Button size="icon" variant="ghost" className="h-7 w-7" onClick={e => { e.stopPropagation(); navigate(`/instagram/posts/${post.id}`); }}>
                            <Eye className="h-4 w-4" />
                          </Button>
                          <Button size="icon" variant="ghost" className="h-7 w-7" onClick={e => { e.stopPropagation(); handleOpenInstagram(post.url); }}>
                            <ExternalLink className="h-4 w-4" />
                          </Button>
                          <Button size="icon" variant="ghost" className="h-7 w-7 text-red-600" onClick={e => { e.stopPropagation(); handleDeleteClick(post); }}>
                            <Trash2 className="h-4 w-4" />
                          </Button>
                        </div>
                      </div>
                    </div>
                  ))
                )}
              </div>
            </div>
          )}
        </CardContent>
      </Card>

      {/* Delete Confirmation Dialog */}
      <Dialog open={deleteDialogOpen} onOpenChange={setDeleteDialogOpen}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>Hapus Postingan</DialogTitle>
            <DialogDescription>
              Apakah Anda yakin ingin menghapus postingan ini? Tindakan ini tidak dapat dibatalkan.
            </DialogDescription>
          </DialogHeader>
          {selectedPost && (
            <div className="py-4">
              <p className="text-sm text-muted-foreground line-clamp-2">
                {selectedPost.caption || 'Postingan tanpa caption'}
              </p>
            </div>
          )}
          <DialogFooter>
            <Button variant="outline" onClick={() => setDeleteDialogOpen(false)}>
              Batal
            </Button>
            <Button variant="destructive" onClick={handleConfirmDelete} disabled={isDeleting}>
              {isDeleting ? (
                <>
                  <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                  Menghapus...
                </>
              ) : (
                'Hapus'
              )}
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>

      {/* Scrape Posts Dialog */}
      <Dialog open={scrapeDialogOpen} onOpenChange={setScrapeDialogOpen}>
        <DialogContent>
          <DialogHeader>
            <DialogTitle>Scrape Postingan Instagram</DialogTitle>
            <DialogDescription>
              Pilih akun yang sudah terhubung untuk mengambil postingan terbaru.
            </DialogDescription>
          </DialogHeader>
          <div className="space-y-4 py-4">
            <div className="space-y-2">
              <Label htmlFor="scrape-account">Akun Instagram</Label>
              <Select value={scrapeUsername} onValueChange={setScrapeUsername}>
                <SelectTrigger>
                  <SelectValue placeholder="Pilih akun..." />
                </SelectTrigger>
                <SelectContent>
                  {accounts.map((account) => (
                    <SelectItem key={account.id} value={account.username}>
                      @{account.username} - {account.full_name}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
            <div className="space-y-2">
              <Label htmlFor="scrape-limit">Jumlah Postingan</Label>
              <Input
                id="scrape-limit"
                type="number"
                min="1"
                max="100"
                value={scrapeLimit}
                onChange={(e) => setScrapeLimit(e.target.value)}
                placeholder="10"
              />
              <p className="text-sm text-muted-foreground">
                Maksimal 100 postingan per scrape
              </p>
            </div>
          </div>
          <DialogFooter>
            <Button variant="outline" onClick={handleScrapeDialogClose} disabled={isScraping}>
              Batal
            </Button>
            <Button onClick={handleScrapePosts} disabled={isScraping || !scrapeUsername}>
              {isScraping ? (
                <>
                  <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                  Memproses...
                </>
              ) : (
                <>
                  <Download className="mr-2 h-4 w-4" />
                  Scrape
                </>
              )}
            </Button>
          </DialogFooter>
        </DialogContent>
      </Dialog>
    </DashboardLayout>
  );
}

export default InstagramPostsPage;
