import { useState } from 'react';
import { DashboardLayout } from '@/components/layout';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { Brain, Loader2, Smile, Frown, Meh } from 'lucide-react';
import { toast } from 'sonner';
import { instagramService } from '@/services';

interface AnalysisResult {
  text: string;
  label: string;
  score: number;
  scores: Record<string, number>;
}

export function IndoBertPage() {
  const [inputText, setInputText] = useState('');
  const [isAnalyzing, setIsAnalyzing] = useState(false);
  const [result, setResult] = useState<AnalysisResult | null>(null);

  const handleAnalyze = async () => {
    if (!inputText.trim()) {
      toast.error('Masukkan teks terlebih dahulu');
      return;
    }

    setIsAnalyzing(true);
    setResult(null);

    try {
      const response = await instagramService.analyzeTextIndoBERT(inputText);
      setResult(response);
    } catch (error) {
      console.error('Failed to analyze:', error);
      toast.error('Gagal menganalisis teks');
    } finally {
      setIsAnalyzing(false);
    }
  };

  const getSentimentIcon = (label: string) => {
    switch (label.toUpperCase()) {
      case 'POSITIF':
      case 'POSITIVE':
        return <Smile className="h-12 w-12 text-green-500" />;
      case 'NEGATIF':
      case 'NEGATIVE':
        return <Frown className="h-12 w-12 text-red-500" />;
      default:
        return <Meh className="h-12 w-12 text-yellow-500" />;
    }
  };

  const getSentimentBadge = (label: string) => {
    switch (label.toUpperCase()) {
      case 'POSITIF':
      case 'POSITIVE':
        return <Badge className="bg-green-500 hover:bg-green-600 text-lg px-4 py-1">Positif</Badge>;
      case 'NEGATIF':
      case 'NEGATIVE':
        return <Badge className="bg-red-500 hover:bg-red-600 text-lg px-4 py-1">Negatif</Badge>;
      default:
        return <Badge className="bg-yellow-500 hover:bg-yellow-600 text-black text-lg px-4 py-1">Netral</Badge>;
    }
  };

  return (
    <DashboardLayout
      breadcrumbs={[
        { label: 'Dashboard', href: '/dashboard' },
        { label: 'Analisis Sentimen' },
        { label: 'IndoBERT' },
      ]}
    >
      <div className="max-w-2xl mx-auto space-y-6">
        {/* Header */}
        <div className="flex items-center gap-3">
          <div className="p-2 rounded-lg bg-purple-100 dark:bg-purple-900">
            <Brain className="h-6 w-6 text-purple-600 dark:text-purple-400" />
          </div>
          <div>
            <h1 className="text-2xl font-bold">Analisis IndoBERT</h1>
            <p className="text-muted-foreground">
              Uji analisis sentimen menggunakan model IndoBERT (Deep Learning)
            </p>
          </div>
        </div>

        {/* Input Card */}
        <Card>
          <CardHeader>
            <CardTitle>Masukkan Teks</CardTitle>
            <CardDescription>
              Ketik atau tempel teks yang ingin dianalisis sentimennya
            </CardDescription>
          </CardHeader>
          <CardContent className="space-y-4">
            <textarea
              className="w-full min-h-32 p-3 border rounded-lg resize-none focus:outline-none focus:ring-2 focus:ring-purple-500 dark:bg-gray-800 dark:border-gray-700"
              placeholder="Contoh: Produk ini sangat bagus dan berkualitas!"
              value={inputText}
              onChange={(e) => setInputText(e.target.value)}
            />
            <Button
              onClick={handleAnalyze}
              disabled={isAnalyzing || !inputText.trim()}
              className="w-full"
            >
              {isAnalyzing ? (
                <>
                  <Loader2 className="mr-2 h-4 w-4 animate-spin" />
                  Menganalisis...
                </>
              ) : (
                <>
                  <Brain className="mr-2 h-4 w-4" />
                  Analisis dengan IndoBERT
                </>
              )}
            </Button>
          </CardContent>
        </Card>

        {/* Result Card */}
        {result && (
          <Card>
            <CardHeader>
              <CardTitle>Hasil Analisis</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="flex flex-col items-center gap-4 py-4">
                {getSentimentIcon(result.label)}
                {getSentimentBadge(result.label)}
                <p className="text-sm text-muted-foreground text-center">
                  Confidence: {(result.score * 100).toFixed(2)}%
                </p>
              </div>

              {/* Score breakdown */}
              <div className="mt-4 pt-4 border-t space-y-3">
                <p className="text-sm font-medium">Detail Skor:</p>
                {Object.entries(result.scores).map(([label, score]) => (
                  <div key={label} className="flex items-center gap-3">
                    <div className="w-16 text-sm capitalize">{label}</div>
                    <div className="flex-1 bg-gray-200 dark:bg-gray-700 rounded-full h-2">
                      <div
                        className={`h-2 rounded-full transition-all ${
                          label.toLowerCase().includes('positif') || label.toLowerCase().includes('positive')
                            ? 'bg-green-500'
                            : label.toLowerCase().includes('negatif') || label.toLowerCase().includes('negative')
                            ? 'bg-red-500'
                            : 'bg-yellow-500'
                        }`}
                        style={{ width: `${score * 100}%` }}
                      />
                    </div>
                    <div className="w-14 text-sm text-right">
                      {(score * 100).toFixed(1)}%
                    </div>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>
        )}
      </div>
    </DashboardLayout>
  );
}

export default IndoBertPage;
