import { BrowserRouter, Routes, Route, Navigate } from 'react-router-dom';
import { AuthProvider } from '@/contexts/AuthContext';
import { ProtectedRoute, GuestRoute } from '@/components/auth';
import { LandingPage, LoginPage, RegisterPage, AccountPage, DashboardPage, InstagramAccountsPage, InstagramPostsPage, PostDetailPage, IndoBertPage, LexiconPage } from '@/pages';
import { Toaster } from '@/components/ui/sonner';
import { ThemeProvider } from '@/components/theme-provider';

function App() {
  return (
    <ThemeProvider defaultTheme="system" storageKey="sentiment-ui-theme">
      <BrowserRouter>
        <AuthProvider>
          <Routes>
            {/* Public routes - redirect to dashboard if authenticated */}
            <Route
              path="/login"
              element={
                <GuestRoute>
                  <LoginPage />
                </GuestRoute>
              }
            />
            <Route
              path="/register"
              element={
                <GuestRoute>
                  <RegisterPage />
                </GuestRoute>
              }
            />

            <Route
              path="/"
              element={
                <GuestRoute>
                  <LandingPage />
                </GuestRoute>
              }
            />

            {/* Protected routes - require authentication */}
            <Route
              path="/dashboard"
              element={
                <ProtectedRoute>
                  <DashboardPage />
                </ProtectedRoute>
              }
            />

            <Route
              path="/account"
              element={
                <ProtectedRoute>
                  <AccountPage />
                </ProtectedRoute>
              }
            />

            {/* Instagram routes */}
            <Route
              path="/instagram/accounts"
              element={
                <ProtectedRoute>
                  <InstagramAccountsPage />
                </ProtectedRoute>
              }
            />
            <Route
              path="/instagram/posts"
              element={
                <ProtectedRoute>
                  <InstagramPostsPage />
                </ProtectedRoute>
              }
            />
            <Route
              path="/instagram/posts/:postId"
              element={
                <ProtectedRoute>
                  <PostDetailPage />
                </ProtectedRoute>
              }
            />

            {/* Sentiment Analysis routes */}
            <Route
              path="/sentiment/indobert"
              element={
                <ProtectedRoute>
                  <IndoBertPage />
                </ProtectedRoute>
              }
            />
            <Route
              path="/sentiment/lexicon"
              element={
                <ProtectedRoute>
                  <LexiconPage />
                </ProtectedRoute>
              }
            />

            {/* 404 - redirect to landing */}
            <Route path="*" element={<Navigate to="/" replace />} />
          </Routes>
          <Toaster position="top-right" richColors closeButton />
        </AuthProvider>
      </BrowserRouter>
    </ThemeProvider>
  );
}

export default App;
